                                <div class="mo-a-address wto">{{address.address}}</div>
                            </div>
                            <div class="mo-a-arrow">
                                <i class="el-icon-arrow-right icon"></i>
                            </div>
                        </div>
                        <div class="m-o-address" v-else>
                            <div>您还未选择任何收货地址</div>
                            <i class="el-icon-arrow-right iicon"></i>
                        </div>
                    </div>
            </div>

<!--            <div class="m-o-count">-->
<!--                <div class="mo-c-line bbGray">-->
<!--                    <div class="mc-lf">商品合计</div>-->
<!--                    <div class="mc-rg">￥587.00</div>-->
<!--                </div>-->
<!--                <div class="mo-c-line">-->
<!--                    <div class="mc-lf">运费</div>-->
<!--                    <div class="mc-rg">￥0.00</div>-->
<!--                </div>-->
<!--            </div>-->
<!--            <div class="m-o-selAdd mt10">-->
<!--                <h1 class="w mb5">选择支付方式</h1>-->
<!--                <div class="w">-->
<!--                    <el-radio-group v-model="form.pay" size="small" class="">-->
<!--                        <el-radio class="w" label="online" border>线上支付（微信、支付宝）</el-radio>-->
<!--                        <el-radio class="w mt10 ml0" label="offline" border>线下支付（银行卡转账）</el-radio>-->
<!--                    </el-radio-group>-->
<!--                </div>-->
<!--            </div>-->

            <div class="m-o-invoice">
                <div class="mo-c-line">
                    <div class="mc-lf">
                       我要开发票
                    </div>
                    <div class="mc-rg">
                        <el-switch v-model="form.needInvoice"></el-switch>
                    </div>
                </div>
            </div>
            <div class="m-o-inBox" v-if="form.needInvoice">
                <div class="m_sel flex f-nowrap">
                    <div class="m_tit">选择类型：</div>
                    <div class="m_choice pb10">
                        <el-radio-group v-model="invoiceType" size="small">
                            <el-radio label="1" border>个人</el-radio>
                            <el-radio label="0" border>企业</el-radio>
                        </el-radio-group>
                    </div>
                </div>
                <div class="pb15" v-if="invoiceType=='1'">
                    <el-form :model="personForm" :rules="personRules" ref="personForm" label-position="left" label-width="90px">
                        <el-form-item label="名称" prop="titleP">
                            <el-input v-model="personForm.titleP"></el-input>
                        </el-form-item>
                        <el-form-item label="地址" prop="addressP">
                            <el-input type="textarea" v-model="personForm.addressP"></el-input>
