<script>
    import {checkOrderInfo,submitOrder,submitOrderSingle, address, getOrderInfo,invoice,addOrEditInvoice,getOpenIdToWx} from '../../service/api'
    import {mapState, mapMutations, mapActions} from 'vuex'
    export default {
        name: 'mOrderConfirm',
        data(){
            return{
                form:{
                    goodsId:'',
                    speId:'',
                    number:'',
                    type:0,//0:购物车下单，1：产品详细页下单
                    needInvoice:false,//是否需要开发票
                    distribution:'normal'//送货方式
                },
                invoiceType:'1',//0是企业，1是个人
                goods:{},
                orderDetail:{},
                address:'',
                personForm:{
                    invoice_id:'',
                    titleP:'',
                    addressP:'',
                    phoneP:''
                },
                companyForm:{
                    invoice_id:'',
                    ename:'',
                    taxnumber:'',
                    address:'',
                    phone:'',
                    bank:'',
                    banknumber:''
                },
                personRules:{
                    titleP: [
                        {required:true,message:"请输入名称",trigger:"blur"}
                    ],
                    addressP: [
                        {required:true,message:"请填写地址",trigger:"blur"}
                    ],
                    phoneP
                        : [
                        { required: true, message: '请输入手机号',trigger:"blur"},
                    ]
                },
                companyRules:{
                    ename: [
                        {required:true, message:"请输入公司名称",trigger:"blur"}
                    ],
                    taxnumber: [
                        { required: true, message: '请输入公司税号',trigger:"blur"},
                    ],
                    address: [
                        {required:true, message:"请填写开票地址",trigger:"blur"}
                    ],
                    phone: [
                        { required: true, message: '请输入电话号码',trigger:"blur"},
                    ],
                    bank:[
                        { required: true, message: '请输入开户银行',trigger:"blur"},
                    ],
                    banknumber:[
                        { required: true, message: '请输入银行账号',trigger:"blur"},
                    ]
                }
            }
        },
        components:{

        },
        mounted(){
            this.getUserInfo();
            this.initData();
        },
        computed:{
            ...mapState([
                'userInfo','addressId'
            ]),
        },
        methods:{
            ...mapActions([
                'getUserInfo'
            ]),
            ...mapMutations([
                'CHOOSE_ADDRESS'
            ]),

            //初始化数据
            async initData(){
                this.form.goodsId = this.$route.query.goodsId||'';
                this.form.speId = this.$route.query.speId||'';
                this.form.number = this.$route.query.number||0;
                this.form.type = this.$route.query.type?1:0;

                if (this.userInfo && this.userInfo.user_id){
                    //获取订单
                    let orderRes = await checkOrderInfo(
                        this.form.speId,
                        this.form.number
                    );
                    this.orderDetail = orderRes;

                    //获取产品
                    this.goods = orderRes.goodsInfo;
                    this.initAddress();

                    //获取发票
                    var invoiceRes = await invoice();
                    for(var i = 0 ; i < invoiceRes.length ; i++){
                        if(invoiceRes[i].type==0){
                            //type=0 企业类型
                            this.companyForm = invoiceRes[i];
                        }else{
                            //type=1 个人类型
                            //this.personForm = invoiceRes[i];
                            this.personForm = {
                                invoice_id:invoiceRes[i].invoice_id,
                                titleP:invoiceRes[i].title,
                                addressP:invoiceRes[i].address,
                                phoneP:invoiceRes[i].phone
                            }
                        }
                    }

                }
            },
            //获取地址信息，第一个地址为默认选择地址
            async initAddress(){
                if (this.userInfo && this.userInfo.user_id){
                    const addressRes = await address();
                    if(this.addressId){
                        for(var i = 0 ; i < addressRes.length;i++){
                            if(addressRes[i].add_id==this.addressId){
                                this.address = addressRes[i];
                            }
                        }
                    }else{
                        for(var i = 0 ; i < addressRes.length;i++){
                            if(addressRes[i].is_default==true){
                                this.address = addressRes[i];
                            }
                        }
                    }
                }
            },
            submitInvoiceForm(formName,id) {
                id = id || '';
                this.$refs[formName].validate(async (valid) => {
                    if (valid) {
                        if(this.invoiceType=='0'){//0是企业；1是个人
                            var res = await addOrEditInvoice(
                                id,//invoice_id
                                '',//title
                                this.invoiceType,//type
                                this.companyForm.taxnumber,//taxnumber
                                this.companyForm.address,//address
                                this.companyForm.banknumber,//banknumber
                                this.companyForm.phone,//phone
                                this.companyForm.bank,//bank
                                this.companyForm.ename//ename
                            )
                        }else{
                            var res = await addOrEditInvoice(
                                id,
                                this.personForm.titleP,
                                this.invoiceType,//type
                                '',//taxnumber
                                this.personForm.addressP,//address
                                '',//banknumber
                                this.personForm.phoneP,//phone
                                '',//bank
                                ''//ename
                            )
                        }
                        if(res){
                            this.$message({
                                message: res.msg,
                                type: res.status==12000?'success':'warning'
                            });
                            if(res.status == 12000){
                                var newRes = await invoice();
                            }
                        }
                    } else {
                        return false;
                    }
                });
            },
            async createOrder(){
                //自提是，addID为空，物流设定时，为获取到的addressId
                var addId ='';
                if(this.form.distribution=='normal'){
                    addId = this.address.add_id?this.address.add_id:'';
                }

                //0:购物车下单-submitOrder，1：产品详细页下单-submitOrderSingle
                if(this.form.type == 0){
                    var res = await submitOrder(
                        this.form.distribution=='normal'?1:0,
                        addId,
                        this.form.needInvoice==false?'':(this.invoiceType=='0'?this.companyForm.invoice_id:this.personForm.invoice_id),
                        this.form.needInvoice==false?0:1
                    )
                    if(res){
                        if(res.status == 12000){
                            //确定订单成功，进入支付页面
                            var ua = window.navigator.userAgent.toLowerCase();
                        　　if (ua.match(/MicroMessenger/i) == 'micromessenger'){
                                //是微信浏览器
                                var tar = await getOpenIdToWx('');
                                window.location.href=tar.url;
                        　　}else{
                                this.$router.push({path:"/order/pay",query:{orderId:''}});
                            }
                        }else{
                            this.$message({
                                message: res.msg,
                                type: 'warning'
                            });
                        }
                    }
                }else{
                    var res = await submitOrderSingle(
                        this.form.distribution=='normal'?1:0,
                        addId,
                        this.form.needInvoice==false?'':(this.invoiceType=='0'?this.companyForm.invoice_id:this.personForm.invoice_id),
                        this.form.needInvoice==false?0:1,
                        this.form.speId?this.form.speId:'',
                        this.form.goodsId?this.form.goodsId:'',
                        this.form.number?this.form.number:''
                    )
                    if(res){
                        if(res.status == 12000){
                            //确定订单成功，进入支付页面
                            var ua = window.navigator.userAgent.toLowerCase();
                        　　if (ua.match(/MicroMessenger/i) == 'micromessenger'){
                                //是微信浏览器
                                var tar = await getOpenIdToWx(res.orderId);
                                window.location.href=tar.url;
                        　　}else{
                                this.$router.push({path:"/order/pay",query:{orderId:res.orderId}});
                            }
                        }else{
                            this.$message({
                                message: res.msg,
                                type: 'warning'
                            });
                        }
                    }
                }
            },
            checkForm(){
                //校验发票
                if(this.form.needInvoice == true){
                    if(this.invoiceType==1){//1是个人，0是企业
                        this.$refs['personForm'].validate(async (valid) => {
                            if (valid) {
                                this.submitInvoiceForm('personForm',this.personForm.invoice_id);
                                this.createOrder();
                            } else {
                                return false;
                            }
                        });
                    }else{
                        this.$refs['companyForm'].validate(async (valid) => {
                            if (valid) {
                                this.submitInvoiceForm('companyForm',this.companyForm.invoice_id);
                                this.createOrder();
                            } else {
                                return false;
                            }
                        });
                    }
                }else{
                    this.createOrder();
                }

            },
            toSelAddress(){
                this.$router.push({path:"/order/address",query:{goodsId:this.form.goodsId,speId:this.form.speId,number:this.form.number,type:this.form.type}});
            }

        },
        created(){

        },
        watch:{
            userInfo: function (){
                this.initData();
            }
        }

    }

</script>

